@mixin flex-end {
    justify-content: flex-end;
}
@mixin flex-start {
    justify-content: flex-start;
}
@mixin text-right {
    text-align: end;
}
@mixin text-left {
    text-align: start;
}


shipping-calculator {
	.shipping-title {
		margin-bottom: 15px;
		font-size: var(--heading-5-fs);
		font-weight: 500;		
	}
	.section-container {
		>.flex {
			@include flex-end;
		}
	}
	.form-field {
		margin-bottom: 20px;
		flex: 0 0 100%;
		max-width: 100%;
		select, input {
			height: 40px;
			padding: 5px 15px;
			border-radius: var(--border-radius);
			border-color: var(--miscellaneous-color);
		}
	}
	.shipping-form {
		align-items: flex-end;
	}
	.state-loading {
		display: none;
	}
	button {
		min-width: 200px;
	}
	.form-button-group {
		button {
			height: 40px;
			padding: 0;
			width: 100%;
		}
	}
	&.calculating {
		.state-default {
			display: none;
		}
		.state-loading {
			display: block;
		}
	}
	.wrapper-response {
		margin-bottom: 50px;
	}
	.response-title {
		margin: 20px 0 0;
	}
	.shipping-list {
		margin-top: 20px;
	} 
	.shipping-item {
		margin: 10px 0;
		font-weight: 500;
	}
	.shipping-cost {
		color: var(--primary-color);
	}
	@media (min-width: 750px) {
		.form-field {
			flex: 1;
		}
	}
	@media (min-width: 1000px) {
		.form-field {
			input, select {
				height: 45px;
			}
		}
		.form-button-group button {
			height: 45px;
		}
	}
	@media (min-width: 1300px) {
		.form-button-group {
			flex: 0 0 100%;
		}
		.form-field {
			flex: 0 0 100%;
		}
	}
}